import { makeStyles } from '@material-ui/core/styles';
import { InputAdornment, OutlinedInput, IconButton } from '@material-ui/core';
import { IconSearch, IconX } from '@tabler/icons';
import { Formik } from 'formik';
import { useState } from 'react';

const useStyles = makeStyles(theme => ({
	searchControl: {
		paddingRight: '16px',
		paddingLeft: '16px',
		'& input': {
			background: 'transparent !important',
			paddingLeft: '5px !important',
		},
		[theme.breakpoints.down('lg')]: {
			width: '250px',
		},
		[theme.breakpoints.down('md')]: {
			width: '100%',
			marginLeft: '4px',
			background: theme.palette.mode === 'dark' ? theme.palette.dark[800] : '#fff',
		},
	},
	startAdornment: {
		fontSize: '1rem',
		color: theme.palette.grey[500],
	},
	resetButton: {
		width: '22px',
		height: '22px',
	},
}));

type Props = {
	onSearch: (t: string) => void;
	onTriggerSearch: (b: boolean) => void;
};

type TForm = {
	search: string;
};

const SearchField = ({ onSearch, onTriggerSearch }: Props) => {
	const [search, setSearch] = useState('');
	const classes = useStyles();

	const onSubmit = (values: TForm) => {
		const { search } = values;
		if (search) onSearch(search);
		onTriggerSearch(Boolean(search));
	};

	return (
		<Formik
			initialValues={{
				search: '',
			}}
			onSubmit={onSubmit}
		>
			{({ handleSubmit, handleChange, handleReset }) => (
				<form onSubmit={handleSubmit}>
					<OutlinedInput
						className={classes.searchControl}
						name='search'
						value={search}
						onChange={e => {
							setSearch(e.target.value);
							handleChange(e);
						}}
						placeholder='Search'
						startAdornment={
							<InputAdornment position='start'>
								<IconSearch
									stroke={1.5}
									size='1rem'
									className={classes.startAdornment}
								/>
							</InputAdornment>
						}
						endAdornment={
							<IconButton
								type='button'
								size='small'
								className={classes.resetButton}
								onClick={() => {
									setSearch('');
									onTriggerSearch(false);
									handleReset();
								}}
							>
								{search && <IconX stroke={1.5} size='1rem' />}
							</IconButton>
						}
						aria-describedby='search-helper-text'
						inputProps={{
							'aria-label': 'weight',
						}}
					/>
				</form>
			)}
		</Formik>
	);
};

export default SearchField;
