import { alpha, makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Toolbar, Typography, Box } from '@material-ui/core';
import SearchField from '../components/SearchField';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		height: '83px',
	},
	highlight: {
		backgroundColor: alpha(
			theme.palette.primary.main,
			theme.palette.action.activatedOpacity
		),
	},
	title: {
		flex: '1 1 100%',
	},
}));

type Props = {
	numSelected: number;
	searchHandler: (text: string) => void;
	triggerSearch: (doSearch: boolean) => void;
};

const OfferToolbar = (props: Props) => {
	const classes = useToolbarStyles();
	const { numSelected, searchHandler, triggerSearch } = props;

	return (
		<Toolbar
			className={clsx(classes.root, {
				[classes.highlight]: numSelected > 0,
			})}
		>
			{numSelected > 0 ? (
				<Typography
					className={classes.title}
					color='inherit'
					variant='subtitle1'
					component='div'
				>
					{numSelected} selected
				</Typography>
			) : (
				<Box sx={{ display: { xs: 'none', md: 'block' } }} component='div'>
					<SearchField onSearch={searchHandler} onTriggerSearch={triggerSearch} />
				</Box>
			)}
		</Toolbar>
	);
};

export default OfferToolbar;
